import React, {Component} from 'react';
import axios from 'axios';

import { exchange } from '../reducers/exchange';
import './Alarm.css'

class Alarm extends Component{
  constructor(props) {
    super(props);

    this.state = {
      alarmList: []
    };
  }

  componentDidMount() {    
    axios.get('Alarm')
    .then( response => {
      this.setState({
        alarmList: response.data
      })
    }) 
    .catch( response => { console.log('err\n'+response); } ); // ERROR
    this.props.handleAlarm()
  }

  render() {
    return (
      <table>
        <thead>
          <th></th><th></th><th></th><th></th><th></th><th></th>
        </thead>
        <tbody>
            { // state    map   
            this.state.alarmList.map((l, i) => {
              return (<tr key={i}>
                <td>{l.exchange_name}</td>
                <td>{l.coin}</td>
                <td>{l.sales_action === "1" ? ("") : ( "" )}</td>
                <td>{l.coin_intent}</td>
                <td>{l.coin_price}</td>
                <td>{l.trans_time}</td>
              </tr>)
            })}
        </tbody>
      </table>
    );
	}
}

export default Alarm;


